@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Playfair Display', serif;
  font-family: 'Poppins', sans-serif;
}

body {
  background-color: #e9ecef;
}

/* font-family: 'Playfair Display', serif; */
/* font-family: 'Poppins', sans-serif; */

a {
  text-decoration: none;
}

/* navbar css */

.navbar {
  font-family: 'Poppins', serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;

  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #ffcc00;
}

.nav-link {
  font-weight: 300 !important;
}

.navbarCategory {
  background-color: #ffcc00;
}

.navDropdown {
  font-family: 'Poppins', serif;
  font-size: 20px;
  display: flex;
  align-items: center;
  column-gap: 8px;
}

.navDropdown span {
  font-weight: 600;
}

.formWidth {
  background-color: #fff;
  width: 560px;
}

.navbar-2 {
  display: flex;
  align-items: center;
  column-gap: 8px;
  font-size: 20px;
}

.navbar-2 span {
  font-weight: 600;
}

.navbar-1 {
  display: flex;
  align-items: center;
  column-gap: 15px;
}

.garisBar {
  width: 1px;
  height: 50px;
  border-left: 1px solid rgba(0, 0, 0, 0.1);
}

.navbarCategoryItem {
  display: flex;
  justify-content: space-between;
  height: 80px;
  align-items: center;
}

.iconBag {
  font-size: 22px;
  margin-left: 10px;
}

.menuNavbar {
  display: flex;
  justify-content: space-between;
}

.navbar-brand {
  font-weight: 700;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 3px;
  display: flex;
  column-gap: 10px;
}

.navbar-brand span {
  margin-top: -2px;
}

.footer {
  color: white;
  padding: 25px;
  background: #ffcc00;
  color: #000;
}

.footer a {
  text-decoration: none;
  color: #000;
  padding: 0 10px;
}

.footer a:hover {
  color: #ffefba;
  border-bottom: 1px solid #ffefba;
}

/* Page Not found */

.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf h1 {
  font-size: 100px;
  font-weight: 700;
}

.pnf h2 {
  font-weight: normal;
}

.pnf-btn {
  color: #000000;
  border: 1px solid #000000;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}

.pnf-btn:hover {
  background-color: #000000;
  color: #fff;
  transition: all 0.3s ease-in;
}

/* register pages */
.register {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
  flex-direction: column;
}

/*filter category*/
.filterCategory {
  font-weight: 500;
  font-family: 'Poppins', sans-serif;
  display: flex;
  flex-direction: column;
}

.filter {
  display: flex;
  margin: 20px 10px;
}

.priceFilter {
  font-weight: 500;
  font-family: 'Poppins', sans-serif;
}

.menuCategory {
  width: 300px;
  padding: 15px 15px;
  background-color: #fff;
  border-radius: 10px;
  height: max-content;
}

.menuCategory h4 {
  font-size: 21px;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
}

.cardProduct {
  display: flex;
}

/* admin dashboard */
.adminDashboard {
  display: flex;
}

.adminDashboardLeft {
  width: 300px;
  padding: 15px 10px;
}

.adminDashboardRight {
  width: 100%;
  padding: 15px 10px;
}
/* user dashboard */
.userDashboard {
  display: flex;
}

.userDashboardLeft {
  width: 300px;
  padding: 15px 10px;
}

.userDashboardRight {
  width: 100%;
  padding: 15px 10px;
}

/* home page */
.cardProduct {
  display: flex;
  column-gap: 20px;
}

.btnFilter {
  display: none;
  align-items: center;
  column-gap: 3px;
  font-weight: 600;
  cursor: pointer;
  font-size: 18px;
}

.btnmenuPanel {
  font-size: 18px;
  display: flex;
  align-items: center;
  column-gap: 4px;
  display: none;
}

/*nav Mobile*/
.navMobile {
  width: 100%;
  height: 50px;
  background-color: #fff;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  position: fixed;
  bottom: 0;
  z-index: 999;
  justify-content: center;
  display: none;
}

.navMobileItem ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 0rem;
  margin-bottom: 0em;
  column-gap: 20px;
}

.navMobileItem ul li {
  padding: 0px 20px;
  font-size: 23px;
}

/*orders*/
.orders {
  display: flex;
  justify-content: space-between;
}

.btnXFilter {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.btnXFilter span {
  font-size: 28px;
  display: none;
}

/*cart page*/
.itemCart {
  display: flex;
  column-gap: 30px;
}

.itemCartImage img {
  width: 300px;
}

.categoryItem {
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1.5px solid rgba(0, 0, 0, 0.1);
  font-size: 30px;
}

.categoryItem:hover {
  background-color: rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

@media screen and (max-width: 1281px) {
  .navbarCategoryItem {
    flex-direction: column-reverse;
    height: 140px;
  }
  .navbar-2 {
    margin-top: 10px;
  }
}

@media only screen and (max-width: 1259px) {
}

@media only screen and (max-width: 992px) {
  .userDashboardLeft {
    display: none;
  }
  .userDashboardLeft.active {
    display: block;
  }
  .adminDashboardLeft {
    display: none;
  }
  .adminDashboardLeft.active {
    display: block;
  }
  .btnmenuPanel {
    display: block;
  }
  .navMobile {
    display: flex;
  }
  .footer {
    padding: 45px;
  }
}

@media only screen and (max-width: 768px) {
  .itemCart {
    flex-direction: column;
  }
  .itemCartTitle {
    margin-top: 10px;
  }
  .itemCartImage img {
    width: 100%;
  }
}

@media only screen and (max-width: 710px) {
  .formWidth {
    width: 400px;
  }
}

@media only screen and (max-width: 547px) {
  .formWidth {
    width: 260px;
  }
}

@media only screen and (max-width: 420px) {
  .formWidth {
    width: 200px;
  }
}
